<template>
  <div class="home min-h-screen bg-gray-900 w-full">
    <!--   -->
    <PageHeader />

    <!--   -->
    <LoadingSpinner
      :loading="isLoading"
      :message="loadingMessage"
    />

    <div class="flex flex-col min-h-screen" :class="{ 'opacity-50': isLoading }">
      <!--   -->
      <FeaturedMovieBanner
        v-if="featuredMovie"
        :movie="featuredMovie"
        @show-detail="showMovieDetail"
        class="w-full"
      />

      <!--   -->
      <div class="flex-grow">
        <TotalMovieSections
          @refresh="loadMovies"
          @show-detail="showMovieDetail"
          @featured-movie="setFeaturedMovie"
        />

      </div>

      <!--    -->
      <MovieDetailModal
        v-if="selectedMovie"
        :movie="selectedMovie"
        @close="selectedMovie = null"
        class="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50"
      />
    </div>

    <!--    -->
    <ScrollToTop class="fixed bottom-8 right-8 z-40"/>
  </div>
</template>

<script setup>
//    
import { ref, onMounted, computed } from 'vue';
import movieService from '@/services/movieService';
import MovieDetailModal from "@/components/movie/MovieDetailModal.vue";
import ScrollToTop from "@/components/layout/ScrollToTop.vue";
import FeaturedMovieBanner from "@/components/home/FeaturedMovieBanner.vue";
import TotalMovieSections from "@/components/home/TotalMovieSections.vue";
import PageHeader from "@/components/layout/PageHeader.vue";
import { useWishlist } from '@/composables/useWishlist';
import LoadingSpinner from '@/components/common/LoadingSpinner.vue'

//     ref 
const isLoading = ref(false);
const loadingMessage = ref('   ...');

const featuredMovie = ref(null);
const selectedMovie = ref(null);

const { wishlist } = useWishlist();
const wishlistMovies = computed(() => wishlist.value);

const setFeaturedMovie = (movie) => {
  featuredMovie.value = movie;
};

const showMovieDetail = async (movie) => {
  console.log('showMovieDetail ', movie); //  
  if (!movie?.id) {
    console.error(' ID ');
    return;
  }

  try {
    isLoading.value = true;
    loadingMessage.value = '    ...';

    console.log('API  , movieId:', movie.id); //   2
    const movieDetails = await movieService.getMovieDetails(movie.id);
    console.log('API :', movieDetails); //   3

    if (movieDetails) {
      selectedMovie.value = movieDetails;
      console.log('selectedMovie :', selectedMovie.value); //   4
    } else {
      console.error('   ');
    }
  } catch (error) {
    console.error('      :', error);
  } finally {
    isLoading.value = false;
  }
};

const loadMovies = async () => {
  try {
    isLoading.value = true;
    loadingMessage.value = '    ...';

    const { data } = await movieService.getPopularMovies();
    if (data.results && data.results.length > 0) {
      featuredMovie.value = data.results[0];
    }
  } catch (error) {
    console.error('   :', error);
  } finally {
    isLoading.value = false;
  }
};

onMounted(() => {
  loadMovies();
});

const handleImageError = (e) => {
  e.target.src = '/placeholder-poster.jpg';
};

// movieService 
console.log('movieService:', movieService); //  


</script>

